<template>
  <div class="topbar">
<!--    <img alt="logo" src="../assets/logo1.png" class="img1" height="48.06" width="69.78">-->
    <img src="../assets/Des.png" style="height: 60px">
    <el-button class="b3" @click="jumpp()">返回主界面</el-button>
    <input ref="uploadpro" className="data-upload" type="file" @change="uploadPro($event)" style="display: none"/>
    <el-button class="b4" @click="openpro()">打开</el-button>

    <input type='file' ref="saveres" @change="triggerFile($event)" style="display: none" webkitdirectory/>

    <el-button class="b5" @click="save()">保存</el-button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "tou",
  components: {

  },


  data() {
    return {

    }
  },

  methods: {
    goTo(path) {
      this.$router.replace(path);
    },
    jumpp() {
      window.location.href =
          "http://localhost"
    },

    uploadPro(input) {
      //支持chrome IE10
      if (window.FileReader) {
        var file = input.target.files[0];
        var reader = new FileReader();
        reader.onload=((event)=>{
          //显示文件
          //this.input_text=event.target.result;
          console.log(event.target.result);
          axios.post('http://localhost:8077/sscm/uploadpro',event.target.result).then(function (response){
            console.log(response)
          });
        })
        console.info(file)
        console.info(reader);
        reader.readAsText(file);

        this.$message({
          message: '项目打开成功！',
          type: 'success'
        });
      }
      else {
        alert("FileReader Not supported by your browser!");
      }
    },

    openpro(){
      this.$refs.uploadpro.click();
    },

    triggerFile(event) {
      var _this = this;
      let file = event.target.files[0];
      console.log(file.webkitRelativePath);
      let name = file.webkitRelativePath;

      axios.post('http://localhost:8077/sscm/saveresult',name).then(function (response){
        console.log(response.data)

        if(response.data==0){
          _this.$message({
            message: '保存失败！',
            type: 'error'
          });
        }
        else{
          _this.$message({
            message: '保存成功！',
            type: 'success'
          });
        }
      });

      event.target.value = '';

    },

    save(){
      this.$refs.saveres.click();
    },

  }

}
</script>

