  },

  methods: {
    goTo(path) {
      this.$router.replace(path);
    },
    jumpp() {
      window.location.href =
          "http://localhost:8090"
    },

    next() {
      if (this.active++ > 6) this.active = 1;
      console.log(this.active)
      if(this.active == 1){
        this.goTo('/DesVer');
      }
      else if(this.active == 2){
        this.goTo('/Des');
      }
      else if(this.active == 3){
        this.goTo('/Dev');
      }
      else if(this.active == 4){
        this.goTo('/Dep');
      }
      else if(this.active == 5){
        this.goTo('/Sys');
      }
      else if(this.active == 6){
        this.goTo('/DC');
      }
      else if(this.active == 7){
        this.goTo('/CK');
      }
      else{
        this.goTo('/DesVer');
      }
    },

    pre() {
      if (this.active-- < 2) this.active = 1;
      console.log(this.active)
      if(this.active == 1){
        this.goTo('/DesVer');
      }
      else if(this.active == 2){
        this.goTo('/Des');
      }
      else if(this.active == 3){
